<template>
    <div>
        <div class="b-title">常用CSS样式</div>
        <div class="b-title2">文字单行显示/三行显示</div>
        <div class="b-info">单行文本溢出隐藏变为...</div>
        <pre class="b-pre">
            <code lang="css">
        p{
          overflow:hidden; /* 隐藏元素溢出内容*/
          white-space:nowrap; /* 单行显示 */
          text-overflow:ellipsis; /* 溢出显示省略号*/
        }
            </code>
        </pre>
        <div class="b-info">多行文本溢出隐藏变为...</div>
        <pre class="b-pre">
            <code lang="css">
        p{
          overflow:hidden; /* 隐藏元素溢出内容*/
          display:-webkit-box; /* 将对象作为弹性伸缩盒子模型显示 */
          -webkit-line-clamp:2; /* 限制在一个块元素显示的文本的行数，即行数设置 */
          -webkit-box-orient:vertical; /* 规定框从上向下垂直排列子元素 */
        }
            </code>
        </pre>

    </div>
</template>

